<template>
  <div>
    <fTitle>
      <template #title>Tabs 标签页</template>
      <template #introduce>分隔内容上有关联但属于不同类别的数据集合。</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法</template>
      <template #introduce>基础的、简洁的标签页。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
      <el-radio-group v-model="tabPosition" style="margin-bottom: 30px;">
    <el-radio-button label="top">top</el-radio-button>
    <el-radio-button label="right">right</el-radio-button>
    <el-radio-button label="bottom">bottom</el-radio-button>
    <el-radio-button label="left">left</el-radio-button>
  </el-radio-group>
<div class="grid-content"><el-tabs type="border-card" :tab-position="tabPosition" style="text-align: start;">
  <el-tab-pane>
    <span slot="label"><i class="el-icon-date"></i> 我的行程</span>
    我的行程
  </el-tab-pane>
  <el-tab-pane label="消息中心">
    <span slot="label"><i class="el-icon-chat-dot-round"></i> 消息中心</span>
    消息中心</el-tab-pane>
  <el-tab-pane label="角色管理">
    <span slot="label"><i class="el-icon-user"></i> 角色管理</span>
    角色管理</el-tab-pane>
  <el-tab-pane label="定时任务补偿">
    <span slot="label"><i class="el-icon-suitcase-1"></i> 定时任务补偿</span>
    定时任务补偿</el-tab-pane>
</el-tabs></div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-tabs v-model="editableTabsValue" type="border-card" editable @edit="handleTabsEdit">
  <el-tab-pane
    :key="item.name"
    v-for="item in editableTabs"
    :label="item.title"
    :name="item.name"
  >
    {{item.content}}
  </el-tab-pane>
</el-tabs>
</div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      tabPosition: 'left',
      editableTabsValue: '2',
      editableTabs: [{
        title: 'Tab 1',
        name: '1',
        content: 'Tab 1 content'
      }, {
        title: 'Tab 2',
        name: '2',
        content: 'Tab 2 content'
      }],
      tabIndex: 2
    }
  },
  methods: {
    handleTabsEdit (targetName, action) {
      if (action === 'add') {
        const newTabName = ++this.tabIndex + ''
        this.editableTabs.push({
          title: 'New Tab',
          name: newTabName,
          content: 'New Tab content'
        })
        this.editableTabsValue = newTabName
      }
      if (action === 'remove') {
        const tabs = this.editableTabs
        let activeName = this.editableTabsValue
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              const nextTab = tabs[index + 1] || tabs[index - 1]
              if (nextTab) {
                activeName = nextTab.name
              }
            }
          })
        }

        this.editableTabsValue = activeName
        this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;}
    }}
</style>
